import React from 'react';

/**
 * Displays security alerts in a panel
 */
function AlertPanel({ alerts, onAcknowledge }) {
  if (!alerts || alerts.length === 0) {
    return <div className="no-alerts">No active alerts</div>;
  }

  return (
    <div className="alert-panel">
      {alerts.slice(0, 5).map((alert) => (
        <div 
          key={alert.id} 
          className={`alert ${getSeverityClass(alert.severity)} alert-item`}
        >
          <div className="alert-header">
            <span className="alert-severity">{alert.severity.toUpperCase()}</span>
            <span className="alert-time">
              {new Date(alert.timestamp).toLocaleString()}
            </span>
          </div>
          <div className="alert-body">
            <div className="alert-type">{alert.alert_type}</div>
            <p>{alert.description}</p>
          </div>
          <div className="alert-footer">
            {!alert.acknowledged && (
              <button 
                className="btn btn-sm btn-outline-secondary"
                onClick={() => onAcknowledge && onAcknowledge(alert.id)}
              >
                Acknowledge
              </button>
            )}
            {alert.acknowledged && (
              <span className="badge bg-secondary">
                Acknowledged by {alert.acknowledged_by}
              </span>
            )}
          </div>
        </div>
      ))}
      
      {alerts.length > 5 && (
        <div className="more-alerts">
          <button className="btn btn-sm btn-link">
            View {alerts.length - 5} more alerts
          </button>
        </div>
      )}
    </div>
  );
}

/**
 * Get CSS class for alert severity
 */
function getSeverityClass(severity) {
  switch (severity.toLowerCase()) {
    case 'critical':
      return 'alert-danger';
    case 'high':
      return 'alert-warning';
    case 'medium':
      return 'alert-primary';
    case 'low':
      return 'alert-info';
    default:
      return 'alert-secondary';
  }
}

export default AlertPanel; 