import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Button, Card, Badge } from 'react-bootstrap';
import NetworkMap from './visualization/NetworkMap';
import AlertPanel from './AlertPanel';
import DeviceList from './DeviceList';
import TrafficChart from './visualization/TrafficChart';
import SecurityTrends from './visualization/SecurityTrends';
import DeviceHealthStatus from './DeviceHealthStatus';
import api from '../services/api';
import './Dashboard.css';

/**
 * Main dashboard component for the ICS Security Monitoring System
 */
function Dashboard() {
  // State variables
  const [devices, setDevices] = useState([]);
  const [alerts, setAlerts] = useState([]);
  const [networkData, setNetworkData] = useState({ devices: [], connections: [] });
  const [trafficData, setTrafficData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [stats, setStats] = useState({
    deviceCount: 0,
    alertCount: 0,
    criticalAlerts: 0,
    protocolStats: {}
  });
  const [timeRange, setTimeRange] = useState('weekly');
  
  // Load initial data
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        
        // Fetch devices
        const devicesResponse = await api.getDevices();
        setDevices(devicesResponse);
        
        // Fetch alerts
        const alertsResponse = await api.getAlerts();
        setAlerts(alertsResponse);
        
        // Fetch network map
        const networkMapResponse = await api.getNetworkMap();
        setNetworkData(networkMapResponse);
        
        // Fetch traffic data
        const trafficResponse = await api.getTrafficVolume();
        setTrafficData(trafficResponse);
        
        // Fetch protocol stats
        const protocolStatsResponse = await api.getProtocolStats();
        
        // Compute statistics
        setStats({
          deviceCount: devicesResponse.length,
          alertCount: alertsResponse.length,
          criticalAlerts: alertsResponse.filter(alert => alert.severity === 'critical').length,
          protocolStats: protocolStatsResponse
        });
        
        setLoading(false);
      } catch (err) {
        console.error("Error fetching data:", err);
        setError("Failed to load dashboard data. Please try again.");
        setLoading(false);
      }
    };
    
    fetchData();
    
    // Set up WebSocket for real-time updates
    const wsProtocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
    const wsUrl = `${wsProtocol}//${window.location.host}/ws`;
    const socket = new WebSocket(wsUrl);
    
    socket.onopen = () => {
      console.log('WebSocket connection established');
    };
    
    socket.onmessage = (event) => {
      const data = JSON.parse(event.data);
      
      if (data.type === 'initial_data') {
        // Handle initial data load
        setDevices(data.devices);
        setAlerts(data.alerts);
      } else if (data.type === 'new_device') {
        // Add new device
        setDevices(prev => [...prev, data.device]);
      } else if (data.type === 'new_alert') {
        // Add new alert
        setAlerts(prev => [data.alert, ...prev]);
        
        // Update critical alert count if necessary
        if (data.alert.severity === 'critical') {
          setStats(prev => ({
            ...prev,
            alertCount: prev.alertCount + 1,
            criticalAlerts: prev.criticalAlerts + 1
          }));
        } else {
          setStats(prev => ({
            ...prev,
            alertCount: prev.alertCount + 1
          }));
        }
      } else if (data.type === 'traffic_update') {
        // Update traffic data
        setTrafficData(prev => [...prev, data.data_point]);
      }
    };
    
    socket.onclose = () => {
      console.log('WebSocket connection closed');
    };
    
    // Clean up on component unmount
    return () => {
      socket.close();
    };
  }, []);
  
  // Handle alert acknowledgement
  const handleAcknowledgeAlert = async (alertId) => {
    try {
      await api.acknowledgeAlert(alertId);
      
      // Update local state
      setAlerts(prev => 
        prev.map(alert => 
          alert.id === alertId 
            ? { ...alert, acknowledged: true, acknowledged_by: 'current_user' } 
            : alert
        )
      );
    } catch (err) {
      console.error("Error acknowledging alert:", err);
      setError("Failed to acknowledge alert. Please try again.");
    }
  };
  
  // Handle device scan initiation
  const handleScanNetwork = async () => {
    try {
      const response = await api.scanDevices({
        network_range: '192.168.1.0/24',
        scan_type: 'basic'
      });
      
      console.log("Scan initiated:", response);
      // Could add a toast notification here
    } catch (err) {
      console.error("Error initiating scan:", err);
      setError("Failed to initiate network scan. Please try again.");
    }
  };

  // Handle analysis initiation
  const handleAnalyzeTraffic = async () => {
    try {
      const response = await api.analyzeTraffic({
        time_range: '24h',
        depth: 'detailed'
      });
      
      console.log("Analysis initiated:", response);
      // Could show a toast notification or update UI to show analysis is in progress
    } catch (err) {
      console.error("Error initiating analysis:", err);
      setError("Failed to initiate traffic analysis. Please try again.");
    }
  };
  
  // Display loading or error state
  if (loading) {
    return (
      <div className="dashboard-loading">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading...</span>
        </div>
        <div className="mt-3">Loading dashboard data...</div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="dashboard-error">
        <div className="alert alert-danger" role="alert">
          <i className="bi bi-exclamation-triangle-fill me-2"></i>
          {error}
        </div>
      </div>
    );
  }
  
  // Render the dashboard
  return (
    <div className="dashboard">
      <Container fluid>
        <div className="dashboard-header">
          <h1>
            <i className="bi bi-shield-lock me-2"></i>
            ICS Security Monitoring System
          </h1>
          <div className="dashboard-controls">
            <Button variant="outline-secondary" className="me-2" onClick={handleAnalyzeTraffic}>
              <i className="bi bi-graph-up me-1"></i> Analyze Traffic
            </Button>
            <Button variant="primary" onClick={handleScanNetwork}>
              <i className="bi bi-search me-1"></i> Scan Network
            </Button>
          </div>
        </div>
        
        {/* Stats Overview */}
        <div className="stats-overview">
          <div className="stat-card">
            <h3>Monitored Devices</h3>
            <p className="stat-value">{stats.deviceCount}</p>
            <Badge bg="success">
              {devices.filter(d => d.is_online).length} Online
            </Badge>
          </div>
          <div className="stat-card">
            <h3>Total Alerts</h3>
            <p className="stat-value">{stats.alertCount}</p>
            <Badge bg={stats.alertCount > 10 ? "warning" : "success"}>
              Last 24 Hours
            </Badge>
          </div>
          <div className="stat-card">
            <h3>Critical Alerts</h3>
            <p className="stat-value" style={{ color: stats.criticalAlerts > 0 ? '#dc3545' : '' }}>
              {stats.criticalAlerts}
            </p>
            <Badge bg={stats.criticalAlerts > 0 ? "danger" : "success"}>
              {stats.criticalAlerts > 0 ? "Action Required" : "All Clear"}
            </Badge>
          </div>
          <div className="stat-card">
            <h3>Active Protocols</h3>
            <p className="stat-value">{Object.keys(stats.protocolStats).length}</p>
            <Badge bg="info">
              Monitoring
            </Badge>
          </div>
        </div>
        
        {/* Main Content */}
        <Row>
          <Col lg={8}>
            <div className="dashboard-panel">
              <h2>Network Topology</h2>
              <NetworkMap data={networkData} />
            </div>
          </Col>
          <Col lg={4}>
            <div className="dashboard-panel">
              <h2>Device Health Overview</h2>
              <DeviceHealthStatus devices={devices} />
            </div>
            <div className="dashboard-panel">
              <h2>Active Alerts</h2>
              <AlertPanel 
                alerts={alerts} 
                onAcknowledge={handleAcknowledgeAlert} 
              />
            </div>
          </Col>
        </Row>
        
        <Row className="mt-4">
          <Col lg={8}>
            <div className="dashboard-panel">
              <h2>Traffic Analysis</h2>
              <TrafficChart data={trafficData} />
            </div>
          </Col>
          <Col lg={4}>
            <div className="dashboard-panel">
              <h2>Security Incident Trends</h2>
              <SecurityTrends 
                data={alerts} 
                timeRange={timeRange} 
              />
            </div>
          </Col>
        </Row>
        
        <Row className="mt-4">
          <Col lg={4}>
            <div className="dashboard-panel">
              <h2>Protocol Distribution</h2>
              <div className="protocol-stats">
                {Object.entries(stats.protocolStats).map(([protocol, count]) => (
                  <div key={protocol} className="protocol-stat-item">
                    <span className="protocol-name">{protocol}</span>
                    <span className="protocol-count">{count}</span>
                    <div 
                      className="protocol-bar" 
                      style={{ 
                        width: `${(count / Math.max(...Object.values(stats.protocolStats))) * 100}%` 
                      }}
                    />
                  </div>
                ))}
              </div>
            </div>
          </Col>
          <Col lg={8}>
            <div className="dashboard-panel">
              <h2>Discovered Devices</h2>
              <DeviceList devices={devices} />
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Dashboard; 