import React from 'react';
import { PieChart, Pie, Cell, ResponsiveContainer, Legend, Tooltip } from 'recharts';

/**
 * Device Health Status component
 * Shows a pie chart of device health status
 */
function DeviceHealthStatus({ devices }) {
  if (!devices || devices.length === 0) {
    return <div className="no-data">No device data available</div>;
  }
  
  // Count devices by health status
  const statusCounts = {
    healthy: 0,
    warning: 0,
    critical: 0,
    offline: 0
  };
  
  devices.forEach(device => {
    if (!device.is_online) {
      statusCounts.offline++;
    } else if (device.risk_score > 70) {
      statusCounts.critical++;
    } else if (device.risk_score > 30) {
      statusCounts.warning++;
    } else {
      statusCounts.healthy++;
    }
  });
  
  // Prepare data for the pie chart
  const data = [
    { name: 'Healthy', value: statusCounts.healthy, color: '#28a745' },
    { name: 'Warning', value: statusCounts.warning, color: '#ffc107' },
    { name: 'Critical', value: statusCounts.critical, color: '#dc3545' },
    { name: 'Offline', value: statusCounts.offline, color: '#6c757d' }
  ].filter(item => item.value > 0);
  
  // Calculate total
  const totalDevices = devices.length;
  
  return (
    <div className="device-health-status">
      <div className="health-header">
        <h3>Device Health Status</h3>
      </div>
      
      <div className="health-chart-container">
        <ResponsiveContainer width="100%" height={220}>
          <PieChart>
            <Pie
              data={data}
              cx="50%"
              cy="50%"
              innerRadius={60}
              outerRadius={80}
              paddingAngle={2}
              dataKey="value"
              label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
              labelLine={false}
            >
              {data.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={entry.color} />
              ))}
            </Pie>
            <Tooltip formatter={(value) => [`${value} devices`, 'Count']} />
          </PieChart>
        </ResponsiveContainer>
      </div>
      
      <div className="health-stats">
        {data.map((status) => (
          <div key={status.name} className="health-stat-item">
            <div className="health-stat-color" style={{ backgroundColor: status.color }}></div>
            <div className="health-stat-name">{status.name}</div>
            <div className="health-stat-value">{status.value}</div>
            <div className="health-stat-percent">
              {((status.value / totalDevices) * 100).toFixed(1)}%
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default DeviceHealthStatus; 