import React from 'react';
import { 
  LineChart, Line, XAxis, YAxis, CartesianGrid, 
  Tooltip, Legend, ResponsiveContainer 
} from 'recharts';

/**
 * Traffic volume chart visualization
 */
function TrafficChart({ data }) {
  // Format data for chart
  const formattedData = data.map(point => ({
    name: new Date(point.timestamp).toLocaleTimeString(),
    packets: point.packet_count,
    bytes: point.byte_count / 1024, // Convert to KB
  }));
  
  if (!data || data.length === 0) {
    return <div className="no-data">No traffic data available</div>;
  }
  
  return (
    <div className="traffic-chart">
      <ResponsiveContainer width="100%" height={300}>
        <LineChart
          data={formattedData}
          margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis yAxisId="left" />
          <YAxis yAxisId="right" orientation="right" />
          <Tooltip />
          <Legend />
          <Line 
            yAxisId="left"
            type="monotone" 
            dataKey="packets" 
            name="Packets" 
            stroke="#8884d8" 
            activeDot={{ r: 8 }} 
          />
          <Line 
            yAxisId="right"
            type="monotone" 
            dataKey="bytes" 
            name="Volume (KB)" 
            stroke="#82ca9d" 
          />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
}

export default TrafficChart; 