import React from 'react';

/**
 * Component to display a list of discovered ICS devices
 */
function DeviceList({ devices }) {
  if (!devices || devices.length === 0) {
    return <div className="no-devices">No devices discovered yet</div>;
  }

  return (
    <div className="device-list">
      <table className="table table-striped">
        <thead>
          <tr>
            <th>IP Address</th>
            <th>Hostname</th>
            <th>Device Type</th>
            <th>Protocols</th>
            <th>Status</th>
            <th>Risk Score</th>
          </tr>
        </thead>
        <tbody>
          {devices.map((device) => (
            <tr key={device.id}>
              <td>{device.ip_address}</td>
              <td>{device.hostname || 'Unknown'}</td>
              <td>{device.device_type || 'Unknown'}</td>
              <td>
                {device.protocols ? 
                  device.protocols.map(p => p.name).join(', ') : 
                  'Unknown'
                }
              </td>
              <td>
                <span className={`badge ${device.is_online ? 'bg-success' : 'bg-danger'}`}>
                  {device.is_online ? 'Online' : 'Offline'}
                </span>
              </td>
              <td>
                <div className="progress">
                  <div 
                    className={`progress-bar ${getRiskClass(device.risk_score)}`} 
                    role="progressbar" 
                    style={{ width: `${device.risk_score}%` }}
                    aria-valuenow={device.risk_score} 
                    aria-valuemin="0" 
                    aria-valuemax="100"
                  >
                    {device.risk_score.toFixed(1)}
                  </div>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}

/**
 * Get CSS class for risk score
 */
function getRiskClass(score) {
  if (score < 30) return 'bg-success';
  if (score < 70) return 'bg-warning';
  return 'bg-danger';
}

export default DeviceList; 